body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}


.content {
  width: 100%;
  height: 800px;
  margin: 50px auto 0px auto;
  display: block;
  background: url(/static/media/lvf-three-heads_web.94978012.jpg) no-repeat center center fixed;
  background-size: cover;
}


/* Menu bar style */

.header-menu {
  display: none;
  width: 100%;
  padding: 5px;
  background-color: #000;
  color: white;
  position: absolute;
  left: 0;
  top: 0;
}

.burger-holder {
    padding: 5px 10px;
    border: white solid 2px;
    border-radius: 15px;
    margin: 6px 20px 6px auto;
    width: 35px;
}

.burger-holder:hover {
  border-color: gold;
}

.burger-bar {
    width: 35px;
    height: 5px;
    background-color: white;
    margin: 6px 0;    
}

.burger-bar:hover {
  background-color: gold;
}


.name-and-link-holder {
  width: 35%;
  background-color: rgba(299,299,299,0.55);
  position: relative;
  top: 82%;
  left: 60%;
  padding-bottom: 10px;
}

@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .name-and-link-holder {
        width: 100%;
    }
}


.name-and-link-holder img {
  width: 100%;
  height: auto;
}


a {
  font-size: 1.2em;
  color: black;
  text-decoration: none;
}


a:hover {
  text-decoration: underline;
}



.footer {
  margin-top: 40px;
  text-align: center;
}
